/* contact.css */
#contact {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center-align the entire section horizontally */
  justify-content: center; /* Center-align the entire section vertically */
  height: 100vh; /* Full viewport height */
  padding: 0px 20px 0px 20px; /* Adjust padding for spacing, remove bottom padding */
  position: static;
  bottom: 0;
}

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 3.125rem; /* Relative unit */
  font-style: normal;
  color: #ffffff;
  margin-bottom: 10px; /* Adjust to reduce space below the heading */
}

p {
  font-size: 1.5rem; /* Increase base font size */
  text-align: center; /* Center-align the paragraph */
  margin: 0; /* Remove default margins */
  margin-top: 5px; /* Add a smaller margin above the paragraph */
}

/* Media queries for responsive design */
@media (max-width: 1024px) {
  p {
    font-size: 1.375rem; /* Adjust font size for tablets */
  }

  h2 {
    font-size: 2.5rem; /* Adjust heading font size for tablets */
  }

  #contact {
    height: unset;
    padding: 15px 15px 0 15px; /* Adjust padding for tablets, remove bottom padding */
  }
  #contact {
    height: 100vh; /* Ensure full viewport height */
    padding: 5px 5px 0 5px; /* Adjust padding for mobile devices, remove bottom padding */
    display: flex;
    flex-direction: column;
    align-items: center; /* Center-align the entire section horizontally */
    justify-content: center; /* Center-align the entire section vertically */
  }
}

@media (max-width: 768px) {
  p {
    font-size: 1.25rem; /* Adjust font size for small screens */
    padding: 0 10px; /* Add padding for better spacing on small screens */
  }

  h2 {
    font-size: 2rem; /* Adjust heading font size for small screens */
  }

  #contact {
    padding: 10px 10px 0 10px; /* Adjust padding for small screens, remove bottom padding */
  }
}

@media (max-width: 480px) {
  p {
    font-size: 1.125rem; /* Adjust font size for mobile devices */
  }

  h2 {
    font-size: 1.5rem; /* Adjust heading font size for mobile devices */
  }

  #contact {
    height: 100vh; /* Ensure full viewport height */
    padding: 5px 5px 0 5px; /* Adjust padding for mobile devices, remove bottom padding */
    display: flex;
    flex-direction: column;
    align-items: center; /* Center-align the entire section horizontally */
    justify-content: center; /* Center-align the entire section vertically */
  }
}

.contact-links {
  display: flex;
  flex-direction: row;
  justify-content: center; /* Center-align links horizontally */
  align-items: center; /* Center-align links vertically */
  margin-top: 15px; /* Optional: Add margin to separate from section content */
  flex-wrap: nowrap; /* Prevent wrapping */
  overflow-x: auto; /* Enable horizontal scrolling if needed */
}

.contact-links a {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center-align icon and text vertically */
  text-decoration: none;
  color: inherit;
  margin: 0 10px; /* Adjust horizontal spacing between links */
  font-size: 1.25rem; /* Use rem for scalable text */
}

.contact-links a:hover {
  color: #0f114e; /* Optional: Change color on hover */
}

/* Media queries for responsive design */
@media (max-width: 1024px) {
  .contact-links a {
    font-size: 1.125rem; /* Adjust icon size for tablets */
    margin: 0 8px; /* Adjust spacing between links for tablets */
  }
}

@media (max-width: 768px) {
  .contact-links a {
    font-size: 1rem; /* Adjust icon size for small screens */
    margin: 0 5px; /* Adjust spacing between links for small screens */
  }
}

@media (max-width: 480px) {
  .contact-links a {
    font-size: 0.875rem; /* Adjust icon size for mobile devices */
    margin: 0 5px; /* Adjust spacing between links for mobile devices */
  }
}
